import React, { Component } from 'react';
import CurrentService from './CurrentService';
import Util from '../common/Util';

export default class extends React.Component {
    static async getInitialProps({ query, req }) {

        // from commercetools (demo-api)
        const categoryId = _.get(query);
        const categories = await Util.fetchCommerceCategories(categoryId);
    
        return (
            <div className="CurrentServices">
                <h2 className="currServ-title title">These are the services we are providing you at the moment:</h2>
                <div className="currentServices-sub">
                    {this.state.products.map((item, i) => (
                        <CurrentService
                            productId={item.sys.id}
                            key={i}
                            productName={item.fields.productName}
                            productCode={item.fields.productCode}
                            productHighlight={item.fields.productHighlight}
                            price={item.fields.price}
                            longDescription={item.fields.longDescription}
                            productShortDescription={item.fields.productShortDescription}
                            image={item.fields.imageLink}>
                        </CurrentService>
                    ))}
                </div>
                <style jsx>
                    {`
                        .CurrentServices {
                            margin-left: auto;
                            margin-right: auto;
                        }
                        .currentServices-sub {
                            display: grid;
                            grid-template-columns: repeat(auto-fit,minmax(340px,1fr));
                        }
                    `}
                </style>
            </div>
        );
    }
}



